html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

#login-container {
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  width: 300px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

#login-container h2 {
  color: #333;
  margin: 0 0 20px;
}

#login-container input[type="email"], #login-container input[type="password"] {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
}

#login-container button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
}

#login-container button:hover {
  background-color: #0056b3;
}

#login-error {
  margin-top: 15px;
}

#map {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.leaflet-tooltip {
  background-color: #fffc;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px;
  font-size: 16px;
  font-weight: bold;
  color: #8b0000 !important;
}

